* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  background-color: #222831;
  color: #eeeeee;
}

.App {
  width: 100%;
  height: calc(100vh - 50px);
  display: flex;
}

.todoApp {
  width: 100%;
}

.todoApp_inner {
  width: 100%;
  max-width: 725px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

@media screen and (max-width: 576px) {
  .todoApp_inner {
    width: 90%;
  }
}

.todoCollectionName {
  font-size: 2rem;
  margin-top: 40px;
}

@media screen and (max-width: 576px) {
  .todoCollectionName {
    font-size: 1.7rem;
    margin-top: 20px;
  }
}

.addTodo_form {
  margin: 5em 0;
  position: relative;
  display: flex;
  align-items: center;
}

.addTodo_input {
  width: 100%;
  padding: 18px 70px;
  border-radius: 15px;
  background-color: transparent !important;
  border: 3px solid #32363e;
  color: #eeeeee;
  font-size: 1.1rem;
  outline: none;
}

.addTodo_btn {
  position: absolute;
  left: 1.7em;
  width: 31px;
  height: 31px;
  background: linear-gradient(43.19deg, #e94560 40.63%, #ffe713 184.65%);
  border-radius: 10px;
  cursor: pointer;
  outline: none;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 576px) {
  .addTodo_form {
    margin: 2em 0;
  }
}

.todoList::-webkit-scrollbar {
  width: 5px;
}

.todoList::-webkit-scrollbar-track {
  background: transparent;
}

.todoList::-webkit-scrollbar-thumb {
  background: #32363e;
  border-radius: 10px;
}

.todoList {
  width: 100%;
  height: 450px;
  overflow-y: scroll;
  list-style: none;
  margin-bottom: 5em;
}

.todo {
  width: 99%;
  height: 60px;
  margin-bottom: 20px;
  font-weight: bold;
  background-color: #32363e;
  background-image: url(/todo_react_app/static/media/todo_bg.53ae1d7c.svg);
  background-position: center;
  border-radius: 15px;
  font-size: 1.3em;
  display: flex;
  align-items: center;
}

.todoCheck {
  width: 70px;
  height: 100%;
  background-color: #3f434b;
  border-radius: 15px 0px 0px 15px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.todoCheck input[type="checkbox"] {
  transition: all 0.15s ease-out;
  appearance: none;
  -webkit-appearance: none;
  width: 20px;
  height: 20px;
  background-color: transparent;
  border: solid 3px #e94560;
  border-radius: 5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.todoCheck input[type="checkbox"]::after {
  content: url(/todo_react_app/static/media/check.c0c4eb84.svg);
  display: none;
}

.todoCheck input[type="checkbox"]:checked {
  background-color: #e94560;
}

.todoCheck input[type="checkbox"]:checked::after {
  display: block;
}

.done {
  text-decoration: line-through;
}

.todoNameAndActions {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 30px 0 30px;
}

.todoNameAndActions span {
  cursor: pointer;
}

.todoNameAndActions span:nth-child(1) {
  margin-right: 20px;
}

@media screen and (max-width: 576px) {
  .todoList {
    min-height: 238px;
    margin-bottom: 1.5em;
  }

  .todo {
    height: 45px;
    border-radius: 8px;
  }

  .todo p {
    font-size: 1.1rem;
  }

  .todoCheck {
    width: 55px;
    border-radius: 8px 0px 0px 8px;
  }

  .todoCheck input[type="checkbox"] {
    width: 17px;
    height: 17px;
    border: solid 2px #e94560;
    border-radius: 3px;
  }

  .todoNameAndActions {
    margin: 0 20px 0 20px;
  }

  .todoNameAndActions_actions svg {
    width: 17px;
  }
}

.center {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.popup {
  width: 450px;
  height: 300px;
  background-color: #eeeeee;
  color: #222831;
  box-shadow: 0px 5px 10px 10px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  box-sizing: border-box;
  z-index: 2;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.editIcon {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 3px solid #e94560;
  text-align: center;
  display: inline-block;
  line-height: 60px;
}

.editTitle {
  font-size: 25px;
  font-weight: 600;
}

.editForm {
  width: 50%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.editForm input {
  width: 100%;
  margin-bottom: 25px;
  padding: 10px 5px;
  border-radius: 10px;
  border: 3px solid #32363e;
  color: #32363e;
  background: #eeeeee;
  font-size: 1.1rem;
  font-weight: 600;
  outline: none;
  text-align: center;
}

.editForm_buttons {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.editForm_buttons button {
  font-size: 1.1rem;
  font-weight: 600;
  color: #e94560;
  padding: 8px 15px;
  outline: none;
  cursor: pointer;
  border: none;
  background: transparent;
}

.editForm_buttons button:nth-child(1) {
  background: #e94560;
  border-radius: 6px;
  color: #f5f5f5;
}

@media screen and (max-width: 576px) {
  .popup {
    width: 80%;
    margin: 0 auto;
  }

  .editTitle {
    font-size: 20px;
  }

  .editForm {
    width: 80%;
  }

  .editForm input {
    font-size: 0.9rem;
  }

  .editForm_buttons button {
    font-size: 0.9rem;
    padding: 6px 13px;
  }

  .editForm_buttons button:nth-child(1) {
    border-radius: 4px;
  }
}

.todoActions {
  width: 80%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-bottom: 40px;
}

.todoActions_done {
  transition: all 0.3s ease;
  padding: 15px 15px;
  color: #eeeeee;
  background-color: #e94560;
  font-size: larger;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.todoActions_delete {
  transition: all 0.3s ease;
  padding: 15px 15px;
  color: #e94560;
  background-color: transparent;
  font-size: larger;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.todoActions_done_disabled {
  transition: all 0.3s ease;
  padding: 15px 15px;
  color: #eeeeee;
  background-color: #3f434b;
  pointer-events: none;
  cursor: none;
  font-size: larger;
  font-weight: bold;
  border-radius: 5px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.todoActions_delete_disabled {
  transition: all 0.3s ease;
  padding: 15px 15px;
  color: #3f434b;
  background-color: transparent;
  font-size: larger;
  font-weight: bold;
  border-radius: 5px;
  cursor: none;
  pointer-events: none;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 576px) {
  .todoActions {
    width: 90%;
    justify-content: space-between;
    margin-bottom: 20px;
  }

  .todoActions_done,
  .todoActions_delete,
  .todoActions_done_disabled,
  .todoActions_delete_disabled {
    padding: 10px 10px;
    font-size: medium;
  }
}

.navbar {
  width: 100%;
  height: 50px;
  background-color: #32363e;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navbarMenu {
  margin-left: 30px;
}

.navbarMenu svg {
  cursor: pointer;
}

.navbarIcons {
  margin-right: 30px;
  display: flex;
  align-items: center;
}

.profile {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-image: url(data:image/png;base64,);
  cursor: pointer;
}

.nav_btn {
  position: unset;
  width: 25px;
  height: 25px;
  border-radius: 8px;
  margin-right: 25px;
}

.navAddTodo_btn_disabled {
  background: #222831;
  pointer-events: none;
  outline: none;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbarIcons span {
  margin-right: 25px;
  cursor: pointer;
}

.navbar_sm {
  /* display: block; */
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  /* height: 50px; */
  /* z-index: 10; */
}

.navbar_sm_inner {
  background-color: #32363e;
  border-top: 1px solid #222831;
  width: 100%;
  padding: 10px 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.navbar_sm .nav_btn {
  margin-right: 0;
}

.sideBar {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 250px;
  height: calc(100% - 50px);
  /* padding: 0 30px; */
  background-color: #32363e;
  border-top: 1px solid #222831;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
  z-index: 1;
  left: -250px;
  transition: all 0.5s ease;
}

.active {
  left: 0;
}

.sideBar p {
  margin-top: 40px;
  padding: 0 30px;
  font-size: 1.2em;
  font-weight: bold;
}

.addCollection_form {
  margin-bottom: 40px;
  padding: 0 30px;
  position: relative;
  display: flex;
  align-items: center;
}

.addCollection_input {
  width: 100%;
  padding: 12px 37px 12px 10px;
  border-radius: 8px;
  background-color: transparent !important;
  border: 2px solid #222831;
  color: #eeeeee;
  font-size: 1rem;
  outline: none;
}

.addCollection_btn {
  position: absolute;
  right: 3em;
  width: 23px;
  height: 23px;
  background: linear-gradient(43.19deg, #e94560 40.63%, #ffe713 184.65%);
  border-radius: 7px;
  cursor: pointer;
  outline: none;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.sideBar ul {
  list-style: none;
  margin-top: 40px;
  width: 100%;
  height: 620px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.sideBar ul::-webkit-scrollbar {
  width: 5px;
}

.sideBar ul::-webkit-scrollbar-track {
  background: transparent;
}

.sideBar ul::-webkit-scrollbar-thumb {
  background: #222831;
  border-radius: 10px;
}

.sideBar li {
  display: flex;
  align-items: center;
  margin-bottom: 25px;
  padding: 15px 30px;
  cursor: pointer;
  transition: all 0.15s ease;
}

.sideBar span {
  min-width: 25px;
  height: 25px;
  margin-right: 17px;
  border-radius: 7px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.collection:hover {
  background-color: #222831;
}

.collection span {
  background: #e94560;
}

