.todoList::-webkit-scrollbar {
  width: 5px;
}

.todoList::-webkit-scrollbar-track {
  background: transparent;
}

.todoList::-webkit-scrollbar-thumb {
  background: #32363e;
  border-radius: 10px;
}

.todoList {
  width: 100%;
  height: 450px;
  overflow-y: scroll;
  list-style: none;
  margin-bottom: 5em;
}

.todo {
  width: 99%;
  height: 60px;
  margin-bottom: 20px;
  font-weight: bold;
  background-color: #32363e;
  background-image: url("../img/todo_bg.svg");
  background-position: center;
  border-radius: 15px;
  font-size: 1.3em;
  display: flex;
  align-items: center;
}

.todoCheck {
  width: 70px;
  height: 100%;
  background-color: #3f434b;
  border-radius: 15px 0px 0px 15px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.todoCheck input[type="checkbox"] {
  transition: all 0.15s ease-out;
  appearance: none;
  -webkit-appearance: none;
  width: 20px;
  height: 20px;
  background-color: transparent;
  border: solid 3px #e94560;
  border-radius: 5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.todoCheck input[type="checkbox"]::after {
  content: url("../img/check.svg");
  display: none;
}

.todoCheck input[type="checkbox"]:checked {
  background-color: #e94560;
}

.todoCheck input[type="checkbox"]:checked::after {
  display: block;
}

.done {
  text-decoration: line-through;
}

.todoNameAndActions {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 30px 0 30px;
}

.todoNameAndActions span {
  cursor: pointer;
}

.todoNameAndActions span:nth-child(1) {
  margin-right: 20px;
}

@media screen and (max-width: 576px) {
  .todoList {
    min-height: 238px;
    margin-bottom: 1.5em;
  }

  .todo {
    height: 45px;
    border-radius: 8px;
  }

  .todo p {
    font-size: 1.1rem;
  }

  .todoCheck {
    width: 55px;
    border-radius: 8px 0px 0px 8px;
  }

  .todoCheck input[type="checkbox"] {
    width: 17px;
    height: 17px;
    border: solid 2px #e94560;
    border-radius: 3px;
  }

  .todoNameAndActions {
    margin: 0 20px 0 20px;
  }

  .todoNameAndActions_actions svg {
    width: 17px;
  }
}
